/* Main style sheet */

body {
    margin: 20px;
    padding: 20px;
    font-family: 'Arial', sans-serif;
    /* background-color: #f8f8f8; */
    color: #333;
}

/* Container to hold the image */
.image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; /* Prevents the image from overflowing the container */
    width: 100%; /* Set to the desired width or 100% to fill its parent */
    height: auto; /* Adjust based on your layout, or set a specific height */
}
  
/* Styles to center and scale the image */
.image-container img {
    max-height: 100px;

    object-fit: contain; /* Ensures the image's aspect ratio is preserved */
}

.margin-1 {
    margin: 10px;
}

.margin-2 {
    margin: 20px;
}

.margin-3 {
    margin: 40px;
}

.margin-4 {
    margin: 80px;
}

.rich-text-box {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: visible;
    margin-left: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
}

/* Specific style sheet for user entry components */

.icon-radio-buttons {
    margin-left: 15px;
}

/* Hide the radio buttons */
.icon-radio-buttons input[type="radio"] {
    display: none;
}
  
/* Style for labels/icons */
.icon-radio-buttons label {
    cursor: pointer;
    color: grey; /* Default icon color */
    transition: color 0.3s ease; /* Smooth transition for color change */
    margin-right: 10px;
    align-items: center; /* Centers items vertically */
}

/* Additional styling for hover effects */
.icon-radio-buttons label:hover {
    color: #0056b3; /* Darker shade when hovered for better UX */
}

.icon-selected {
    color: #007bff; /* Ensure this class applies the desired styles */
}

/* Formatting for options rows */
.radio-row {
    margin-bottom: 20px; /* Adds space between rows */
    display: flex; /* Aligns items in a row */
    align-items: center; /* Centers items vertically */
    justify-content: center;
}

.radio-row input[type="radio"] {
    margin-right: 10px; /* Adds space between the radio button and its label */
    display: flex;
}

.radio-row label {
    margin-right: 30px; /* Adds space between each label-radio pair */
    cursor: pointer; /* Changes the cursor to a pointer when hovering over the label */
    display: flex;
}

.entry-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
}

.main-content {
    display: block;
    margin-left: 180px;
}

.timestamp {
    color:rgb(170, 170, 170);
    margin-right: 350px;
    font-size: 12px; /* Smaller font size for the timestamp */
}

.card {
    background-color: white;
    overflow: scroll;
    display: flex;
    margin-top: 80px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    padding: 25px;
    max-width: 500px;
    height: 150%;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.card-buttons {
    background-color: white; /* White background */
    border: none; /* Borderless */
    cursor: pointer; /* Change cursor to pointer to indicate it's clickable */
    padding: 10px; /* Padding around the icon for better clickability */
    border-radius: 50%; /* Makes the button round */
}
  
.card-buttons:hover {
    background-color: #f0f0f0; /* Light grey background on hover */
}

.card-button-container {
    display: flex;
}

.card-change-buttons {
    margin-left: 300px;
}

.card-move-buttons {
    align-self: flex-start;
}

.card-list {
    padding-inline-start: 0px;
    margin-bottom: 50px;
}

.entry-option {
    margin-top: 10px;
    margin-bottom: 10px;
}

.footer {
    margin-top: 50px;
}
  
/* Ensure the editor itself also respects this width */
.ql-editor {
    max-width: 100%;
}

.quill {
    width: 500px; /* Set the fixed width of your choice */
    margin: auto; /* Center the editor if desired */
}

/* Testing out card stacking */
.stacked-container {
    display: flex;
    flex-direction: column;
    position: relative; /* Establishes a positioning context */
    height: 700px; /* Set the desired height large enough to contain the stacked items */
}
  
.content {
    position: absolute;
    top: 0; /* Aligns the top edge of the element with the top of its containing block */
    left: 0; /* Aligns the left edge of the element with the left of its containing block */
    width: 100%; /* Ensures the content fills the container's width */
    /* Optionally, set a specific height or padding */
}

/* For left sidebar */
.sidebar {
    height: 100vh; /* Full-height: remove this if you want "auto" height */
    width: 180px; /* Set the width of the sidebar */
    position: fixed; /* Fixed Sidebar (stay in place on scroll) */
    z-index: 1; /* Stay on top */
    top: 0; /* Stay at the top */
    left: 0;
    background-color: #111; /* Black */
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 20px;
}
  
.sidebar-button {
    display: block; /* Make buttons take the full width of the container */
    width: 100%; /* Full width */
    padding: 10px; /* Some padding for spacing */
    text-align: center; /* Align text to the left */
    border: none; /* Remove borders */
    background: none; /* Transparent background */
    outline: none; /* Remove outline */
    color: rgb(255, 255, 255); /* Button text color */
    transition: 0.3s; /* Smooth transition for hover effect */
    font-size: 18px; /* Increase font size */
  }
  
/* Hover effect for buttons */
.sidebar-button:hover {
    background-color: #b7b6b6; /* Light grey background on hover */
    color: #000000; /* Darker text color on hover */
    margin-left: 10px;
}
  
/* Active class for the buttons */
.sidebar-button.active {
    background-color: #ffffff; /* Add a green color to the "active"/current button */
    margin-left: 10px;
    color: rgb(0, 0, 0);
}

.sidebar-create-button {
    display: block; /* Make buttons take the full width of the container */
    width: 100%; /* Full width */
    padding: 10px; /* Some padding for spacing */
    text-align: center;
    border: none; /* Remove borders */
    background: none; /* Transparent background */
    outline: none; /* Remove outline */
    color: rgb(255, 255, 255); /* Button text color */
    transition: 0.3s; /* Smooth transition for hover effect */
    font-size: 18px; /* Increase font size */
}

.sidebar-create-button:hover {
    margin-left: 10px;
    background-color: #b7b6b6; /* Light grey background on hover */
    color: #000000; /* Darker text color on hover */
}

.sidebar-create-entry {
    margin-left: 15px;
    color: white;
    font-size: 14px;
}

.journal-container {
    position: relative;
}

/* Stack button */
.stack-button {
    width: 100px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color: transparent;
    position: relative;
    /* overflow: hidden; */
    border-radius: 7px;
    cursor: pointer;
    transition: all .3s;
    padding: 0px;
}
  
.svgContainer {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    backdrop-filter: blur(0px);
    letter-spacing: 0.8px;
    border-radius: 10px;
    transition: all .3s;
    border: 1px solid rgba(156, 156, 156, 0.466);
    color: white;
}
  
.BG {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: #181818;
    z-index: -1;
    border-radius: 10px;
    pointer-events: none;
    transition: all .3s;
}
  
.stack-button:hover .BG {
    transform: rotate(35deg);
    transform-origin: bottom;
}

.stack-button:hover .svgContainer {
    background-color: rgba(156, 156, 156, 0.466);
    backdrop-filter: blur(4px);
}

.rectangular-button {
    display: inline-block; /* Allows setting width and height */
    width: 76px; /* Set the width of the button */
    height: 26px; /* Set the height of the button */
    background-color: #535353; /* Button color */
    color: white; /* Text color */
    border: none; /* Remove border */
    cursor: pointer; /* Change mouse cursor on hover */
    text-align: center; /* Center the text inside the button */
    line-height: 13px; /* Align text vertically */
    border-radius: 0px; /* Optional: rounds the corners */
    text-decoration: none; /* Remove underline from links if button is an <a> tag */
    font-size: 14px; /* Text size */
    margin-top: 5px;
}

.rectangular-button:hover {
    background-color: #2c2c2c; /* Darker shade on hover for visual feedback */
}

.page-title-container {
    display: flex; /* Use flexbox layout */
    align-items: center; /* Align items vertically in the center */
    padding: 5px;
}

.page-title-container .button {
    text-align: center; /* Align text */
    border: none; /* Remove borders */
    background: none; /* Transparent background */
    outline: none; /* Remove outline */
    font-size: 18px;
}

.page-title-container .button .title {
    margin-right: 10px;
}

.rename-page-label {
    width: 200px; /* Adjust the width as needed */
    height: 40px; /* Adjust the height as needed */
    padding: 5px; /* Adds space inside the box, making it effectively larger */
    font-size: 18px; /* Increases the font size for better readability */
}
